/* global Office, Word */

// â€” quick log so we know this bundle loaded â€”
console.log("â­ preposition.js loaded");

let state = {
  errors: [],
  currentIndex: 0,
  isChecking: false
};

const HIGHLIGHT_COLOR = "#FFC0CB";
const NOTIF_ID        = "noErrors";

//â€“â€“ Helpers â€“â€“//
function clearNotification(id) {
  if (
    Office.NotificationMessages &&
    typeof Office.NotificationMessages.deleteAsync === "function"
  ) {
    Office.NotificationMessages.deleteAsync(id);
  }
}
function showNotification(id, options) {
  if (
    Office.NotificationMessages &&
    typeof Office.NotificationMessages.addAsync === "function"
  ) {
    Office.NotificationMessages.addAsync(id, options);
  }
}

//â€“â€“ Core logic â€“â€“//
function determineCorrectPreposition(rawWord) {
  if (!rawWord) return null;
  const word = rawWord.normalize("NFC");
  const match = word.match(/[\p{L}0-9]/u);
  if (!match) return null;
  const first = match[0].toLowerCase();

  const unvoiced = new Set(['c','Ä','f','h','k','p','s','Å¡','t']);
  const numMap   = {
    '1':'e','2':'d','3':'t','4':'Å¡','5':'p',
    '6':'Å¡','7':'s','8':'o','9':'d','0':'n'
  };

  if (/\d/.test(first)) {
    return unvoiced.has(numMap[first]) ? "s" : "z";
  }
  return unvoiced.has(first) ? "s" : "z";
}

//â€“â€“ Exported commands â€“â€“//
export async function checkDocumentText() {
  console.log("â–¶ checkDocumentText()", { isChecking: state.isChecking });
  if (state.isChecking) return;
  state.isChecking = true;
  clearNotification(NOTIF_ID);

  try {
    await Word.run(async context => {
      console.log("â†’ Word.run start");

      // clear old highlights
      state.errors.forEach(e => e.range.font.highlightColor = null);
      state.errors = [];
      state.currentIndex = 0;

      // find all lone â€œsâ€ and â€œzâ€
      const opts = { matchCase: false, matchWholeWord: true };
      const sR = context.document.body.search("s", opts);
      const zR = context.document.body.search("z", opts);
      sR.load("items"); zR.load("items");
      await context.sync();

      const raw = [...sR.items, ...zR.items];
      console.log("â†’ raw hits:", raw.length);

      // filter out anything that isnâ€™t exactly â€œsâ€ or â€œzâ€
      const candidates = raw.filter(r =>
        ["s","z"].includes(r.text.trim().toLowerCase())
      );
      console.log("â†’ filtered candidates:", candidates.length);

      let errors = [];
      for (const prep of candidates) {
        // ** FIX 1 ** Use the enum, not a string:
        const after = prep.getRange(Word.RangeLocation.after);

        // ** FIX 2 ** Expand to the next word:
        after.expandTo(Word.TextRangeUnit.word);

        after.load("text");
        await context.sync();

        const nextWord = after.text.trim();
        if (!nextWord) continue;

        const actual = prep.text.trim().toLowerCase();
        const expect = determineCorrectPreposition(nextWord);
        if (expect && actual !== expect) {
          errors.push({ range: prep, suggestion: expect });
        }
      }

      state.errors = errors;
      console.log("â†’ mismatches found:", errors.length);

      if (!errors.length) {
        showNotification(NOTIF_ID, {
          type: "informationalMessage",
          message: "ðŸŽ‰ No â€˜sâ€™/â€˜zâ€™ mismatches!",
          icon: "Icon.80x80",
          persistent: false
        });
      } else {
        // highlight them & jump to the first
        errors.forEach(e => e.range.font.highlightColor = HIGHLIGHT_COLOR);
        await context.sync();
        errors[0].range.select();
      }
    });
  } catch (e) {
    console.error("checkDocumentText error", e);
    showNotification("checkError", {
      type: "errorMessage",
      message: "Check failed; please try again.",
      persistent: false
    });
  } finally {
    state.isChecking = false;
  }
}

export async function acceptCurrentChange() {
  console.log("â–¶ acceptCurrentChange()", { i: state.currentIndex, tot: state.errors.length });
  if (state.currentIndex >= state.errors.length) return;

  try {
    await Word.run(async context => {
      const err = state.errors[state.currentIndex];
      err.range.insertText(err.suggestion, Word.InsertLocation.replace);
      err.range.font.highlightColor = null;
      await context.sync();

      state.currentIndex++;
      if (state.currentIndex < state.errors.length) {
        state.errors[state.currentIndex].range.select();
      }
    });
  } catch (e) {
    console.error("acceptCurrentChange error", e);
    showNotification("acceptError", {
      type: "errorMessage",
      message: "Failed to apply change. Please re-run the check.",
      persistent: false
    });
  }
}

export async function rejectCurrentChange() {
  console.log("â–¶ rejectCurrentChange()", { i: state.currentIndex });
  if (state.currentIndex >= state.errors.length) return;

  try {
    await Word.run(async context => {
      const err = state.errors[state.currentIndex];
      err.range.font.highlightColor = null;
      await context.sync();

      state.currentIndex++;
      if (state.currentIndex < state.errors.length) {
        state.errors[state.currentIndex].range.select();
      }
    });
  } catch (e) {
    console.error("rejectCurrentChange error", e);
    showNotification("rejectError", {
      type: "errorMessage",
      message: "Failed to reject. Please re-run the check.",
      persistent: false
    });
  }
}

export async function acceptAllChanges() {
  console.log("â–¶ acceptAllChanges()", { tot: state.errors.length });
  if (!state.errors.length) return;

  try {
    await Word.run(async context => {
      for (const err of state.errors) {
        err.range.insertText(err.suggestion, Word.InsertLocation.replace);
        err.range.font.highlightColor = null;
      }
      await context.sync();
      state.errors = [];
    });
  } catch (e) {
    console.error("acceptAllChanges error", e);
    showNotification("acceptAllError", {
      type: "errorMessage",
      message: "Failed to apply all changes.",
      persistent: false
    });
  }
}

export async function rejectAllChanges() {
  console.log("â–¶ rejectAllChanges()", { tot: state.errors.length });
  if (!state.errors.length) return;

  try {
    await Word.run(async context => {
      state.errors.forEach(e => e.range.font.highlightColor = null);
      await context.sync();
      state.errors = [];
    });
  } catch (e) {
    console.error("rejectAllChanges error", e);
    showNotification("rejectAllError", {
      type: "errorMessage",
      message: "Failed to clear changes.",
      persistent: false
    });
  }
}
